
@font-face {
    font-family: 'Marvin';
    src: url('assets/fonts/marvin-font.otf');
}

* {
    margin: 0;
    padding: 0;
}

html, body, main {
    height: 100%;
}

body {
    font-family: 'Marvin';
}

main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

canvas {
    border: 7px solid black;
    border-radius: 7px;
}